import $ from 'jquery';
import 'fullpage.js/dist/jquery.fullpage.js';

$(function() {
    //$('#fullpage').fullpage();
    //$('#fullpage').fullpage({autoScrolling: true});
});

{ "use strict";

class menuBlock {

  constructor() {
    this.tmpX = 0;
    this.backgroundCircle = document.querySelector('.backgroundCircle');
    this.menuBlock = document.querySelector('.menuBlock');
    this.menuHumburger = document.querySelector('#menuHumburger');
    this.menuHumburger.addEventListener('click', () => {
      this.openClose();
    });
    window.addEventListener('resize', () => {
      document.getElementsByClassName('menuBlock')[0].children[1].style.display = 'none';
      this.backgroundCircle.style['right'] = '57%';
      this.backgroundCircle.style['width'] = '25px';
      this.backgroundCircle.style['height'] = '25px';
      this.backgroundCircle.style['top'] = "33px";
      this.backgroundCircle.style['display'] = "none";
      this.menuBlock.style['position'] = 'absolute';
    })
  }

  openClose = () => {
    if (this.tmpX == 0) {this.open();} else {this.close();}
  }

  open = () => {
    this.menuBlock.style['position'] = 'fixed';
    this.backgroundCircle.style['display'] = "block";
    let summBack = this.backgroundCircle.offsetLeft+this.backgroundCircle.offsetWidth;
    this.backgroundCircle.style['right'] = (document.getElementsByClassName('menuBlock')[0].offsetWidth-summBack)+"px";
    this.backgroundCircle.style['width'] = '25px';
    this.backgroundCircle.style['height'] = '25px';
    this.backgroundCircle.style['top'] = "33px";

  let openId = setInterval(() => {
  this.tmpX = this.tmpX + 1;
  this.menuHumburger.style.backgroundPosition = "-"+this.tmpX+"00% -100%";
  this.backgroundCircle.style['width'] = (parseInt(this.backgroundCircle.style['width']) + 250)+"px";
  this.backgroundCircle.style['height'] = (parseInt(this.backgroundCircle.style['height']) + 250)+"px";
  this.backgroundCircle.style['right'] = (parseInt(this.backgroundCircle.style['right']) - 125)+"px";
  this.backgroundCircle.style['top'] = (parseInt(this.backgroundCircle.style['top']) - 95)+"px";
  if (this.tmpX == 9) {clearInterval(openId); document.getElementsByClassName('menuBlock')[0].children[1].style.display = 'block';}
}, 100);
  }

  close = () => {
    let summBack = this.backgroundCircle.offsetLeft+this.backgroundCircle.offsetWidth;
    this.backgroundCircle.style['right'] = (document.getElementsByClassName('menuBlock')[0].offsetWidth-summBack)+"px";
    this.backgroundCircle.style['width'] = this.backgroundCircle.offsetWidth+'px';
    this.backgroundCircle.style['height'] = this.backgroundCircle.offsetWidth+'px';
    this.backgroundCircle.style['top'] = this.backgroundCircle.offsetTop+'px';
    document.getElementsByClassName('menuBlock')[0].children[1].style.display = 'none';
  let closeId = setInterval(() => {
  this.tmpX = this.tmpX - 1;
  this.menuHumburger.style.backgroundPosition = "-"+this.tmpX+"00% -100%";
  this.backgroundCircle.style['width'] = (parseInt(this.backgroundCircle.style['width']) - 250)+"px";
  this.backgroundCircle.style['height'] = (parseInt(this.backgroundCircle.style['height']) - 250)+"px";
  this.backgroundCircle.style['right'] = (parseInt(this.backgroundCircle.style['right']) + 125)+"px";
  this.backgroundCircle.style['top'] = (parseInt(this.backgroundCircle.style['top']) + 95)+"px";
  if (this.tmpX == 0) {clearInterval(closeId);
  this.backgroundCircle.style['display'] = "none";
  this.menuBlock.style['position'] = 'absolute';
  }
}, 100);
  }
  }

  let object = new menuBlock();
}
